import styled, { css } from 'styled-components';

export const StyledAvatarComponent = styled.div`
  display: flex;
  gap: 1rem;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }

  ${(size) =>
    size === 'sm' &&
    css`
      width: 3rem;
      height: 3rem;
    `}

  ${(size) =>
    size === 'ml' &&
    css`
      width: 4rem;
      height: 4rem;
    `}

  ${(size) =>
    size === 'lg' &&
    css`
      width: 5rem;
      height: 5rem;
    `}
`;
